var getScroll = function() {
  //pageXOffset = window.pageXOffset !== undefined ? window.pageXOffset : (document.compatMode == "CSS1Compat" ? window.document.documentElement.scrollLeft : window.document.body.scrollLeft),
    
  var pageYOffset = window.pageYOffset !== undefined ? window.pageYOffset : (document.compatMode == "CSS1Compat" ? window.document.documentElement.scrollTop : window.document.body.scrollTop);

  if (typeof getScroll.y == 'undefined') {
    // getScroll.x = pageXOffset;
    getScroll.y = pageYOffset;
  }
  if (typeof getScroll.distanceY == 'undefined') {
    // getScroll.distanceX = pageXOffset;
    getScroll.distanceY = pageYOffset;
  } else {
    // getScroll.distanceX = pageXOffset - getScroll.x;
    getScroll.distanceY = pageYOffset - getScroll.y;
  }
  //diffX = getScroll.x - pageXOffset,
  var diffY = getScroll.y - pageYOffset;

  getScroll.direction = diffY <= 0 ? 'down' :
    diffY > 0 ? 'up' : 'first';

  // getScroll.x = pageXOffset;
  getScroll.y = pageYOffset;
};

