var s_countdown_start = function(){
  if(typeof(s_countdown_all)==='undefined')
    return false;
    $('.s_countdown_block .s_countdown_timer, .c_countdown_timer').each(function() {
      var that = $(this), finalDate = $(this).data('countdown'), gmDate = $(this).data('gmdate'), id = that.data('id-product'), countdown_pro = $(this).hasClass('countdown_pro');
      var s_countdown_now = new Date();
      var s_countdown_utc = new Date(s_countdown_now.getTime() + s_countdown_now.getTimezoneOffset() * 60000);
      if ((s_countdown_all || $.inArray(id, s_countdown_id_products) > -1) && (new Date(gmDate)>s_countdown_utc))
      {
          that.countdown(finalDate).on('update.countdown', function(event) {
              if(s_countdown_style)
              {
                var format = '<div><i class="icon-clock"></i>%D '+((event.offset.totalDays == 1) ? s_countdown_lang.day : s_countdown_lang.days)+' %H : %M : %S</div>';
                if(countdown_pro)
                  format = '%D '+((event.offset.totalDays == 1) ? s_countdown_lang.day : s_countdown_lang.days)+' %H : %M : %S';
              }
              else
              {
                var format = '<div><span class="countdown_number">%D</span><span class="countdown_text">'+((event.offset.totalDays == 1) ? s_countdown_lang.day : s_countdown_lang.days)+'</span></div><div><span class="countdown_number">%H</span><span class="countdown_text">'+s_countdown_lang.hrs+'</span></div><div><span class="countdown_number">%M</span><span class="countdown_text">'+s_countdown_lang.min+'</span></div><div><span class="countdown_number">%S</span><span class="countdown_text">'+s_countdown_lang.sec+'</span></div>';
                if(countdown_pro)
                    format = '%D '+((event.offset.totalDays == 1) ? s_countdown_lang.day : s_countdown_lang.days)+' %H '+s_countdown_lang.hrs+' %M '+s_countdown_lang.min+' %S '+s_countdown_lang.sec+'';
              }
              that.html(event.strftime(format));
          }).on('finish.countdown',function(event){
              window.location.reload(true);
          });
          if(countdown_pro)
              that.closest('.countdown_outer_box').addClass('counting');
          else
              that.addClass('counting');
      }
  });
  $('.s_countdown_block .s_countdown_perm, .c_countdown_perm, .countdown_pro_perm').each(function() {
      if (s_countdown_all || $.inArray($(this).data('id-product'), s_countdown_id_products) > -1)
          $(this).addClass('counting');
  });
};

function thumb_grid_mater(dom){
  dom.click(function(event){
    event.preventDefault();
    let index = dom.index(this);
    if(typeof($('.pro_gallery_top')[0].swiper)!=='undefined')
      $('.pro_gallery_top')[0].swiper.slideTo(index);
    dom.removeClass('clicked_thumb').eq(index).addClass('clicked_thumb');
    return false;        
  }); 
}
