function update_pro_img_swiper_buttons(){
    var tm_gallery_dom = $('.tm_gallery');
    if(tm_gallery_dom.length)
    {
        $.each(tm_gallery_dom, function(){
            if(typeof this.swiper !== 'undefined')
            {
                if($(this.swiper.slides).length==$(this.swiper.slides).filter('.swiper-slide-visible').length)
                {
                    $(this.swiper.params.nextButton).hide();
                    $(this.swiper.params.prevButton).hide();
                }
                else
                {
                    $(this.swiper.params.nextButton).show();
                    $(this.swiper.params.prevButton).show();
                }
            }
        });
    }
}
function pro_img_swiper_master()
{
  //
  var tm_gallery_dom = $('.tm_gallery');
  if(tm_gallery_dom.length)
  {
    $.each(tm_gallery_dom, function(){
      var that = $(this);
      if(that.hasClass('swiper_inited'))
        return ;
      var is_tm_gallery_thumbs = that.hasClass('tm_gallery_thumbs');
      var initial_slide = that.find('.swiper-slide').index(that.find('.tm_cover'));
      var mySwiper_options = $.extend({}, swiper_default_options, {
            nextButton: $('.swiper-button-next', this),
            prevButton: $('.swiper-button-prev', this),
            slidesPerView: is_tm_gallery_thumbs ? 'auto' : 1,
            onSlideChangeEnd: function(swiper){
              var galler_target = $(swiper.container).hasClass('tm_gallery_thumbs') ? $(swiper.container).closest('.ajax_block_product').find('.tm_gallery_top') : $(swiper.container).closest('.ajax_block_product').find('.tm_gallery_thumbs');
              if(galler_target.length && typeof(galler_target[0].swiper)!=='undefined')
              {
                galler_target[0].swiper.slideTo(swiper.activeIndex);
                if(!is_tm_gallery_thumbs)
                  galler_target[0].swiper.slides.removeClass('clicked_thumb').eq(swiper.activeIndex).addClass('clicked_thumb');
              }
            },
            onInit: function(swiper){
              $(swiper.container).addClass('swiper_inited');
              if($(swiper.slides).length==$(swiper.slides).filter('.swiper-slide-visible').length)
                  $(swiper.container).find('.swiper-button').hide();
              else
                  $(swiper.container).find('.swiper-button').show();
            },
            initialSlide: initial_slide>0 ? initial_slide : 0,
            nested: that.closest('.ajax_block_product').hasClass('swiper-slide'),
            lazyLoading: that.data('lazyload')==1 ? true : false,
            spaceBetween: is_tm_gallery_thumbs ? 3 : 0,
            watchSlidesProgress: true,
            watchSlidesVisibility: true,
            loop: false
      });
      if(is_tm_gallery_thumbs)
      {
        mySwiper_options = $.extend(mySwiper_options, {
          onClick: function(swiper){
              var galler_target = $(swiper.container).closest('.ajax_block_product').find('.tm_gallery_top');
              if(galler_target.length && typeof(galler_target[0].swiper)!=='undefined')
                galler_target[0].swiper.slideTo(swiper.clickedIndex);
              $(swiper.slides).removeClass('clicked_thumb').eq(swiper.clickedIndex).addClass('clicked_thumb');
            }
        });
      }
      new Swiper(this, mySwiper_options);
    });
  }
}
function popup_master(pops){
  var magnific_groups = {};
    $(pops).each(function() {
      var id = $(this).data('group') ? $(this).attr('group') : 'others';
      if(!magnific_groups[id]) {
        magnific_groups[id] = [];
      } 
      magnific_groups[id].push(this);
    });
    $.each(magnific_groups, function() {
      $(this).magnificPopup({
          type: 'image',
          tLoading: '',
          image: {
                verticalFit: sttheme.popup_vertical_fit==1
            },
          removalDelay: 500, //delay removal by X to allow out-animation
          callbacks: {
            beforeOpen: function() {
              // just a hack that adds mfp-anim class to markup 
               this.st.image.markup = this.st.image.markup.replace('mfp-figure', 'mfp-figure mfp-with-anim');
               this.st.mainClass = 'mfp-zoom-in';
            }
          },
          gallery:{
            enabled:true
          }
        });
    });
}
function popup_video_master(pops){
    $.each(pops, function() {
      $(this).magnificPopup({
          type: 'iframe',
          preloader: false,
          fixedContentPos: false
        });
    });
}
function popup_inline_master(pops){
    $.each(pops, function() {
      $(this).magnificPopup({
          type: 'inline',
          removalDelay: 500,
          callbacks: {
            beforeOpen: function() {
               this.st.mainClass = 'mfp-zoom-in';
            }
          },
          preloader: false,
          fixedContentPos: false
        });
    });
}
