function sticky_filters_master(){
  if(sttheme.filter_position!=3 || sttheme.is_mobile_device || $(window).width()<992)
    return false;

  var stikcy_dom = $('.desktop_device #horizontal_filters');

  if(!stikcy_dom.length || stikcy_dom.parent().hasClass('sticky-wrapper'))
    return false;

  var waypoint_item = new Waypoint.Sticky({
    element: stikcy_dom[0],
    handler: function(direction) {
      if (direction == 'down'){
        $(this.element).width($(this.element).closest('#center_column').width());
        /*let stuck_items = $('.stuck').not(this.element);
        if(stuck_items.length)
        {
          let stuck_items_height = 0;
          $.each(stuck_items,function(){
            stuck_items_height += $(this).outerHeight();
          });
          $(this.element).css('top', stuck_items_height);
        }*/
        //unable to tell which be stiky first, so do the same twice, the first time might be correct
        $(this.element).css('top', $('#st_header.stuck').outerHeight()+$('#top_extra #st_mega_menu_container.stuck').outerHeight()+$('#mobile_bar.stuck').outerHeight());

        sticky_filters_timer = setTimeout(() => {
          $(this.element).css('top', $('#st_header.stuck').outerHeight()+$('#top_extra #st_mega_menu_container.stuck').outerHeight()+$('#mobile_bar.stuck').outerHeight());
        }, 200);//make sure sticky filters after sticky header and menu.
      }
      else if (direction == 'up'){
        clearTimeout(sticky_filters_timer);
        $(this.element).width('auto').css('top', '');
      }
    },
    offset: function() {
      return $(this.element).outerHeight()+$('#st_header.stuck').outerHeight()+$('#top_extra #st_mega_menu_container.stuck').outerHeight()+$('#mobile_bar.stuck').outerHeight();
    }
  });
}

function column_watcher()
{
  if(sttheme.sticky_column){
    var sticky_column = $('body#category '+(sttheme.sticky_column==1 ? '#left_column' : '#right_column')+' .main_column_box');
    let stikcy_height = $('#st_header.stuck').outerHeight()+$('#top_extra #st_mega_menu_container.stuck').outerHeight()+$('#mobile_bar.stuck').outerHeight();
    if(sticky_column.hasClass('stuck') && (Waypoint.viewportHeight()-stikcy_height)<sticky_column.outerHeight())
    {
      if(getScroll.direction == 'up')
      {
        if(stiker_column_remeber && stiker_column_remeber!='up')
        {
          if(getScroll.y > sticky_column.offset().top+stikcy_height)
            set_column_abs(sticky_column, sticky_column.offset().top-$('body#category '+(sttheme.sticky_column==1 ? '#left_column' : '#right_column')).offset().top);
          else if(getScroll.y < sticky_column.offset().top+stikcy_height)
          {
            if(sticky_column.offset().top<($('body#category '+(sttheme.sticky_column==1 ? '#left_column' : '#right_column')).offset().top+sticky_column.outerHeight()))
              sticky_column.addClass(stuck);
            set_column_fix(sticky_column, stikcy_height);
          }
        }
        stiker_column_remeber = 'up';
      }
      else if(getScroll.direction == 'down')
      {
        if(stiker_column_remeber && stiker_column_remeber!='down')
        {
          if(getScroll.y < (sticky_column.offset().top+sticky_column.outerHeight()))
            set_column_abs(sticky_column, sticky_column.offset().top-$('body#category '+(sttheme.sticky_column==1 ? '#left_column' : '#right_column')).offset().top);
          else
            set_column_fix(sticky_column, Waypoint.viewportHeight()-sticky_column.outerHeight());
        }
        stiker_column_remeber = 'down';
      }
    }
  }
  return true;
}
function set_column_abs(column, top){
  column.css({
              top: top
            }).addClass('stuck_abs');
  // Waypoint.refreshAll();
}
function set_column_fix(column, top){
  column.css({
              top: top
            }).removeClass('stuck_abs');
  // Waypoint.refreshAll();
}
